.SignIn {
    text-align: center;
}

form {
    margin: 0 auto;
    padding: 10px;
}

label {
    font-family: 'Gill Sans', 'Gill Sans MT', Calibri, 'Trebuchet MS', sans-serif;
    color: #111;
    margin-bottom: 10px;
}

input[type=text], .widebox {
    width: 25%;
    padding: 12px 20px;
    margin: 8px 0;
    box-sizing: border-box;
    border: 2px solid #ccc;
    border-radius: 4px;
    font-size: 16px;
}

.widebox {
    width: 50%;
    height: 100px;
    resize: none;
}

button[type=submit] {
    padding: 14px 25px;
    margin: 8px 0px;
}

.History {
    text-align: center;
    flex: 1;
}

ol {
    box-shadow: 0px 0px 10px rgba(0, 0, 0, 0.2);
    padding: 10px 25%;
    margin: 10px 5px;
    border-radius: 5px;
    background-color: rgb(245, 245, 245);
}

ol > li {
    color: black;
}